.home__wrapper {
  width: 280px;
  margin: 3rem auto 0 auto;
  position: relative;
}
.home__text {
  width: 100vw;
  position: relative;
}
.home__title {
  width: 280px;
  margin: 0 auto;
}
.home__title h1 {
  font-family: var(--raleway);
  font-size: 1.3rem;
  line-height: 1.8rem;
  margin-top: 1rem;
}
.home__desc {
  width: 270px;
  margin: 0 auto;
}
.home__desc p {
  font-size: 0.8rem;
  margin-top: 1rem;
  color: var(--cyan);
}
.home__btn {
  width: 200px;
  background-color: var(--blue);
  height: 40px;
  margin: 1.5rem auto;
  border-radius: 20px;
}
.home__btn button {
  border: none;
  background: none;
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--white);
}
.curvy_image {
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: -1;
}
@media screen and (min-width: 767px) {
  .home__wrapper {
    width: 500px;
  }
  .home__text {
    width: 100vw;
  }
  .home__title {
    width: 500px;
    margin: 0 auto;
  }
  .home__desc {
    width: 500px;
    margin: 0 auto;
  }
}
@media screen and (min-width: 1024px) {
  .home__wrapper {
    width: 100%;
  }
  .home__text {
    width: 100%;
    padding-top: 1.5rem;
  }
  .home__title {
    width: 700px;
    margin: 0 auto;
  }
  .home__title h1 {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
  .home__desc {
    width: 100%;
  }
  .home__desc p {
    font-size: 1.3rem;
    width: 620px;
    margin: 1rem auto;
  }
  .home__btn {
    width: 290px;
    height: 55px;
    border-radius: 30px;
  }
  .home__btn button {
    font-size: 1rem;
  }
}
