.testimonial {
  background-color: var(--main-bg);
  padding-bottom: 15rem;
}
.testimonial__wrapper img {
  width: 20px;
  margin-left: 1.2rem;
}
.testimonial__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  grid-gap: 1rem;
}
.testimonial__container {
  display: grid;
  height: 150px;
  background-color: var(--intro-bg);
  margin: 0 1rem;
  overflow: hidden;
  border-radius: 5px;
  padding: 1rem;
  font-size: 14px;
}
.testimonial__container p {
  width: 230px;
  font-size: 0.6rem;
  color: var(--cyan);
}
.person__content {
  display: flex;
  flex-direction: column;
  gap: 2px;
  width: 100px;
}
.person img {
  width: 30px;
  border-radius: 50%;
  margin-right: 0.5rem;
}
.person__content h4 {
  font-size: 0.7rem;
}
.person__content p {
  font-size: 0.5rem;
}

@media screen and (min-width: 1024px) {
  .testimonial__container {
    height: 200px;
    padding: 1.5rem 1rem;
  }
  .testimonial__container p {
    width: 100%;
    font-size: 0.9rem;
  }
  .person__content {
    width: 100%;
  }
  .person__content p {
    font-size: 0.7rem;
  }
  .testimonial__wrapper img {
    width: 60px;
    position: relative;
    bottom: -17px;
    left: -12px;
  }
  .testimonial__content{
    position: relative;
    z-index: 1;
  }
}
