.footer {
  background-color: var(--footer-bg);
  position: absolute;
  width: 100%;
}
.footer__container {
  width: 300px;
}
.footer__content {
  width: 120px;
  margin-left: 1rem;
  margin-bottom: 2rem;
}
.footer__contact {
  margin-top: 2rem;
  margin-bottom: 3rem;
}
.footer__contact p {
  font-size: 0.8rem;
}
.footer__location,
.footer__phone,
.footer__mail {
  width: 280px;
  margin-bottom: 1rem;
  color: var(--cyan);
}
.footer__icon {
  width: 30px;
  margin-right: 1rem;
  font-size: 1rem;
  color: var(--white);
}
.location {
  margin-left: 10px;
  margin-top: 2px;
  margin-right: 23px;
  width: 3rem;
}
.footer__links {
  margin-bottom: 2rem;
  margin-left: 0.5rem;
}
.footer__links ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer__links ul li {
  font-size: 0.8rem;
  color: var(--cyan);
}
.social__links {
  gap: 1rem;
}
.social__links a {
  font-size: 1rem;
  padding: 0.7rem;
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 200px;
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.copyright {
  font-size: 0.7rem;
  padding: 0.5rem 0;
  color: var(--cyan);
  text-align: center;
}
@media screen and (min-width: 767px) {
  .footer__container {
    display: flex;
    width: 700px;
    justify-content: space-around;
    align-items: flex-start;
  }
  .footer__contact {
    margin-top: 0;
  }
  .copyright {
    text-align: center;
  }
}
@media screen and (min-width: 1024px) {
  .footer__container {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: flex-start;
  }
  .footer__content {
    width: 180px;
  }
  .footer__contact p {
    font-size: 1rem;
  }
  .footer__links ul li {
    font-size: 1rem;
  }
  .copyright {
    text-align: center;
  }
}
