.productive {
  padding: 6rem 0;
  background-color: var(--main-bg);
}
.productive__img {
  width: 270px;
  margin: 0 auto;
}
.productive__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  padding-left: 0.5rem;
  min-width: 290px;
  margin: 2rem auto;
}
.productive__content h4 {
  font-size: 1rem;
  font-family: var(--raleway);
}
.productive__content p {
  font-size: 0.8rem;
  color: var(--cyan);
}
.icon {
  margin-left: 0.5rem;
  background-color: var(--cyan);
  border-radius: 10px;
}
.productive__content a {
  color: var(--blue);
  font-size: 0.8rem;
  width: 150px;
  position: relative;
}
.productive__content a::after {
  content: "";
  width: 135px;
  height: 1px;
  background-color: var(--white);
  position: absolute;
  top: 25px;
}
.productive__content .icon {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

@media screen and (min-width: 767px) {
  .productive__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}
@media screen and (min-width: 1024px) {
  .productive__img {
    width: 565px;
  }
  .productive__content h4 {
    font-size: 2.5rem;
    width: 400px;
  }
  .productive__content P {
    font-size: 1rem;
  }
  .productive__content a{
    font-size: 1rem;
    width: 200px;
  }
  .productive__content a::after{
    width: 170px;
  }
}
