.feature__cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0.5rem;
  text-align: center;
}
.feature__img img {
  width: 50px;
  margin-bottom: 1.8rem;
}
.feature__text {
  width: 260px;
}
.feature__text h4 {
  font-family: var(--raleway);
  letter-spacing: 1px;
  margin-bottom: 0.8rem;
}
.feature__text p {
  font-size: 0.7rem;
  line-height: 1.1rem;
  font-weight: lighter;
  color: var(--cyan);
}
@media screen and (min-width: 767px) {
  .feature__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 1rem;
  }
}

@media screen and (min-width: 1024px) {
  .feature__text {
    width: 365px;
  }
  .feature__text h4 {
    font-size: 1.2rem;
  }
  .feature__text p {
    font-size: 0.9rem;
    line-height: 1.3rem;
  }
  .feature__img img {
    width: 75px;
  }
}
