.subscribe {
  background-color: var(--footer-bg);
}
.subscribe__wrapper {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  max-width: 300px;
  height: 280px;
  text-align: center;
  padding: 2rem 1rem;
  gap: 1rem;
  background-color: var(--intro-bg);
  border-radius: 10px;
  position: relative;
  top: -150px;
  margin: auto;
}
.subscribe__wrapper h1 {
  font-family: var(--raleway);
  font-size: 1rem;
}
.subscribe__wrapper p {
  font-size: 0.7rem;
  width: 230px;
  line-height: 1rem;
  margin-bottom: 0.5rem;
}
.subscribe__content {
  gap: 1rem;
}
.subscribe__content input {
  width: 230px;
  height: 40px;
  border-radius: 30px;
  padding-left: 0.8rem;
  background-color: var(--white);
  font-family: var(--raleway);
}
.subscribe__content button {
  width: 230px;
  height: 40px;
  border-radius: 30px;
  background-color: var(--blue);
  color: var(--white);
  font-weight: 600;
  font-family: var(--raleway);
}

@media screen and (min-width: 767px) {
  .subscribe__wrapper {
    max-width: 600px;
  }
  .subscribe__wrapper h1{
    font-size: 1.5rem;
  }
  .subscribe__wrapper p{
    width: 400px;
  }
  .subscribe__content {
    flex-direction: row;
  }
  .subscribe__content input {
    width: 350px;
  }
  .subscribe__content button {
    width: 150px;
  }
  
}

@media screen and (min-width: 1024px) {
  .subscribe__wrapper {
    max-width: 900px;
  }
  .subscribe__wrapper h1 {
    font-size: 2rem;
  }
  .subscribe__content {
    flex-direction: row;
  }
  .subscribe__content input {
    width: 500px;
    height: 50px;
  }
  .subscribe__content button {
    width: 200px;
    height: 50px;
  }
  .subscribe__wrapper p {
    font-size: 0.9rem;
    width: 700px;
    line-height: 1.3rem;
  }
}
